import { ActionTypes } from "../contants/action-types"

export const itemListReducer = (state = [], { type, payload }) => {
    switch (type) {
        case ActionTypes.ITEM_LIST:
            return [...payload];
        default:
            return state;
    }
}

export const itemDetailReducer = (state = {}, { type, payload }) => {
    switch (type) {
        case ActionTypes.ITEM_DETAIL:
            return { ...payload };
        default:
            return state;
    }
}