// @mui material components
import Card from "@mui/material/Card";
import { useEffect } from "react";
import { useHistory } from "react-router-dom";

// Soft UI Dashboard React components
import SuiBox from "components/SuiBox";
import SuiTypography from "components/SuiTypography";
import SuiButton from "components/SuiButton";

// Soft UI Dashboard React example components
import Table from "layouts/Table";
import { pages, pagesTitle } from "links/pages";

// Custom styles for the Tables
import styles from "./styles";

// Data
import ListModel from "../model/list";
import { getItemList } from "../service";
import { Link } from 'react-router-dom';
import { useDispatch, useSelector } from "react-redux";
import { setItemList } from "redux/action/itemAction";

function ItemList() {
    const classes = styles();
    const history = useHistory();
    const { columns, modelList, modelListInit, modelListEmpty } = ListModel;
    const itemList = useSelector(state => state.itemList);
    const dispatch = useDispatch();
    const handleView = (id, name) => {
        history.push({
            pathname: pages.ITEM_VIEW,
            search: "?" + (name.replace(" ", "-").toLowerCase()),
            state: {
                id,
                name
            }
        });
    }
    async function loadData() {
        await getItemList().then((res) => {
            if (res.flag) {
                if (Object.keys(res.data).length) {
                    dispatch(setItemList(modelList(res.data, handleView)));
                } else {
                    dispatch(setItemList(modelListEmpty()));
                }
            }
        });
    }

    useEffect(() => {
        loadData();
    }, []);

    return (
        <SuiBox py={3}>
            <SuiBox mb={3}>
                <Card>
                    <SuiBox display="flex" justifyContent="space-between" alignItems="center" p={3}>
                        <SuiTypography variant="h6">{pagesTitle.ITEM_LIST}</SuiTypography>
                        <Link to={pages.ITEM_ADD}>
                            <SuiButton buttonColor="secondary">Add Item</SuiButton>
                        </Link>
                    </SuiBox>
                    <SuiBox customClass={classes.tables_table}>
                        {itemList.length == 0 ? (
                            <Table columns={columns} rows={modelListInit()} />
                        ) : (
                            <Table columns={columns} rows={itemList} />
                        )}
                    </SuiBox>
                </Card>
            </SuiBox>
        </SuiBox>
    );
}

export default ItemList;
