// @mui material components
import Card from "@mui/material/Card";
import Grid from '@mui/material/Grid';
import { Controller } from "react-hook-form";

// Soft UI Dashboard React components
import SuiTypography from "components/SuiTypography";
import SuiBox from "components/SuiBox";
import SuiButton from "components/SuiButton";
import SuiInput from "components/SuiInput";
import PropTypes from "prop-types";

import productIcon from "assets/images/product.png";

const ItemForm = ({ control, handleSubmit, onSubmitHandler, isNewItem, errors }) => {
    let cnt = 0, errorList = [];
    for (const key in errors) {
        errorList[cnt] = (
            <SuiTypography key={key} textColor="error" variant="caption" component="li">{errors[key].message}</SuiTypography>
        );
        cnt++;
    }
    return (
        <SuiBox py={3}>
            <SuiBox mb={3}>
                <Card>
                    <SuiBox pt={2} pb={3} px={3}>
                        <form onSubmit={handleSubmit(onSubmitHandler)}>
                            <Grid container spacing={2} direction="row">
                                <Grid item xs={12} lg={3}>
                                    <Grid container spacing={1} alignItems="center" direction="column">
                                        <Grid item xs={12} >
                                            <SuiBox
                                                style={{ border: "1px solid #eee" }}
                                                borderRadius="lg"
                                                boxShadow="sm"
                                                p={2}
                                                m={4}
                                                mt={1}
                                                mb={1}
                                            >
                                                <img style={{ margin: "auto", height: "100%", width: "100%" }} src={productIcon} />
                                            </SuiBox>
                                        </Grid>
                                        <Grid item xs={12} >
                                            <Controller
                                                name="image"
                                                control={control}
                                                render={({ field }) => (
                                                    <SuiButton
                                                        variant="contained"
                                                        component="label"
                                                    >
                                                        Select File
                                                        <input
                                                            {...field}
                                                            type="file"
                                                            hidden
                                                        />
                                                    </SuiButton>
                                                )}
                                            />

                                        </Grid>
                                    </Grid>
                                </Grid>
                                <Grid item xs={12} lg={8}>
                                    <Grid container spacing={2}>
                                        <Grid item xs={12}>
                                            <SuiTypography variant="subtitle2" component="div">Name</SuiTypography>
                                            <Controller
                                                name="name"
                                                control={control}
                                                render={({ field, fieldState }) => {
                                                    return (
                                                        <>
                                                            <SuiInput {...field} error={fieldState.invalid} />
                                                            <SuiTypography textColor="error" variant="caption" component="span">{fieldState.error?.message}</SuiTypography>
                                                        </>
                                                    )
                                                }}
                                            />
                                        </Grid>
                                        <Grid item xs={12}>
                                            <SuiTypography variant="subtitle2" component="div">Code</SuiTypography>
                                            <Controller
                                                name="code"
                                                control={control}
                                                render={({ field, fieldState }) => {
                                                    return (
                                                        <>
                                                            <SuiInput {...field} error={fieldState.invalid} />
                                                            <SuiTypography textColor="error" variant="caption" component="span">{fieldState.error?.message}</SuiTypography>
                                                        </>
                                                    )
                                                }}
                                            />
                                        </Grid>
                                        <Grid item xs={12} lg={6}>
                                            <SuiTypography variant="subtitle2" component="div">Min Qty</SuiTypography>
                                            <Controller
                                                name="min_qty"
                                                control={control}
                                                render={({ field, fieldState }) => {
                                                    return (
                                                        <>
                                                            <SuiInput {...field} error={fieldState.invalid} />
                                                            <SuiTypography textColor="error" variant="caption" component="span">{fieldState.error?.message}</SuiTypography>
                                                        </>
                                                    )
                                                }}
                                            />
                                        </Grid>
                                        <Grid item xs={12} lg={6}>
                                            <SuiTypography variant="subtitle2" component="div">Life</SuiTypography>
                                            <Controller
                                                name="expire_time"
                                                control={control}
                                                render={({ field, fieldState }) => {
                                                    return (
                                                        <>
                                                            <SuiInput {...field} error={fieldState.invalid} />
                                                            <SuiTypography textColor="error" variant="caption" component="span">{fieldState.error?.message}</SuiTypography>
                                                        </>
                                                    )
                                                }}
                                            />
                                        </Grid>
                                    </Grid>
                                </Grid>
                                <Grid item xs={9}>
                                    {errorList}
                                </Grid>
                                <Grid item xs={2}>
                                    <SuiButton style={{ float: "right" }} type="submit" buttonColor={isNewItem ? "primary" : "secondary"}>
                                        {isNewItem ? "Save" : "Update"}
                                    </SuiButton>
                                </Grid>
                            </Grid>
                        </form>
                    </SuiBox>
                </Card>
            </SuiBox>
        </SuiBox >
    );
}

ItemForm.defaultProps = {
    isNewItem: true,
};

ItemForm.propTypes = {
    isNewItem: PropTypes.bool,
    control: PropTypes.object.isRequired,
    handleSubmit: PropTypes.func.isRequired,
    onSubmitHandler: PropTypes.func.isRequired,
};

export default ItemForm;