import { getApiRequest as getApi, alertService } from '_services';

const getApiRequest = async (requestData) => {
    const result = await getApi({
        ...requestData
    }).then(function (response) {
        return { flag: true, status: response.status, message: "Success", data: response.data };
    }).then(function (finalJson) {
        return finalJson;
    }).catch((error) => {
        // if (error.status === 422) {
        //     alertService.error(error.message, { keepAfterRouteChange: true });
        // }
        return { flag: false, status: error.status, message: "Error", data: error.data };
    });
    return result;
}

async function getItemList() {
    return await getApiRequest({
        url: "/store/item"
    }).then((result) => {
        return result;
    });
}

async function getItemDetail(id) {
    return await getApiRequest({
        url: "/store/item/view&id=" + id
    }).then((result) => {
        return result;
    });
}

async function getItemDelete(id) {
    return await getApiRequest({
        url: "/store/item/delete&id=" + id,
        method: "delete"
    }).then((result) => {
        return result;
    });
}

async function addItem(idata) {
    return await getApiRequest({
        url: "/store/item/create",
        method: "post",
        data: { ...idata }
    }).then((result) => {
        return result;
    });
}

async function updateItem(id, idata) {
    return await getApiRequest({
        url: "/store/item/update&id=" + id,
        method: "put",
        data: { ...idata }
    }).then((result) => {
        return result;
    });
}

export {
    getItemList,
    getItemDetail,
    addItem,
    updateItem,
    getItemDelete
};