
// Soft UI Dashboard React example components
import { useEffect, useMemo } from "react";
import { useHistory } from "react-router-dom";
import { useSnackbar } from 'notistack';
import { pages, pagesTitle } from "links/pages";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";
import { useDispatch, useSelector } from "react-redux";
import { setItemDetail } from "redux/action/itemAction";
import ItemForm from "./_form";
// Custom styles for the Tables

// Service
import { updateItem, getItemDetail } from "../service"

const schema = yup.object({
    name: yup.string().required("Name cannot be blank").max(100),
    code: yup.string().required("Code cannot be blank").max(100),
    min_qty: yup.number().positive().integer().required("Min Qty cannot be blank"),
    expire_time: yup.number().positive().integer("Life must be number"),
});

function ItemUpdate() {
    const { enqueueSnackbar } = useSnackbar();
    const itemData = useSelector(state => state.itemDetail);
    const { id, name } = useSelector(state => state.navPath["curr"]);
    const history = useHistory();
    const dispatch = useDispatch();
    async function loadData() {
        await getItemDetail(id)
            .then((res) => {
                if (res.flag) {
                    dispatch(setItemDetail(res.data));
                }
            });
    }
    useEffect(() => {
        if (Object.keys(itemData).length === 0 && id !== undefined) {
            loadData();
        }
        return () => dispatch(setItemDetail({}));
    }, [id]);

    const { handleSubmit, control, setValue, reset } = useForm({
        resolver: yupResolver(schema),
        reValidateMode: 'onChange',
        keepDirty: true,
        defaultValues: useMemo(() => {
            return {
                name: itemData.name,
                code: itemData.code,
                min_qty: itemData.min_qty,
                expire_time: itemData.expire_time,
            };
        }, [itemData])
    });

    useEffect(() => {
        if (itemData) {
            reset({
                name: itemData.name,
                code: itemData.code,
                min_qty: itemData.min_qty,
                expire_time: itemData.expire_time,
            }, {
                keepDirty: true
            });
        }
    }, [itemData]);
    async function onSubmitHandler(data) {
        await updateItem(id, data).then((res) => {
            if (res.flag) {
                enqueueSnackbar("Item update success", {
                    variant: 'success',
                });
                history.push({
                    pathname: pages.ITEM_VIEW,
                    search: "?" + (name.replace(" ", "-").toLowerCase()),
                    state: {
                        id,
                        name
                    }
                });
            } else {
                enqueueSnackbar("Item update failed", {
                    variant: 'error',
                });
            }
        });
    };
    return (
        <ItemForm
            onSubmitHandler={onSubmitHandler}
            handleSubmit={handleSubmit}
            control={control}
            isNewItem={false}
        />
    );
}

export default ItemUpdate;
