import { useEffect } from "react";
// import { useHistory } from "react-router-dom";

import { useHistory } from "react-router-dom";
// Soft UI Dashboard React components
import SuiBox from "components/SuiBox";
import SuiButton from "components/SuiButton";
import Icon from "@mui/material/Icon";
import SuiTypography from "components/SuiTypography";
import Grid from "@mui/material/Grid";
import Skeleton from '@mui/material/Skeleton';
import { useSnackbar } from 'notistack';
// Soft UI Dashboard React components
import { useDispatch, useSelector } from "react-redux";
import { setItemDetail } from "redux/action/itemAction";
import { pages } from "links/pages";
// Service
import { deleteAlert } from "_services";
import { getItemDetail, getItemDelete } from "../service";

function ItemView() {
    const { enqueueSnackbar } = useSnackbar();
    const history = useHistory();
    const itemData = useSelector(state => state.itemDetail);
    const dispatch = useDispatch();
    const { id, name } = useSelector(state => state.navPath["curr"]);

    async function loadData() {
        await getItemDetail(id)
            .then((res) => {
                if (res.flag) {
                    dispatch(setItemDetail(res.data));
                }
            });
    }
    useEffect(() => {
        if (id !== undefined) {
            loadData();
        }
        return () => dispatch(setItemDetail({}));
    }, [id]);

    const updateItem = () => {
        history.push({
            pathname: pages.ITEM_UPDATE,
            search: "?" + (name.replace(" ", "-").toLowerCase()),
            state: {
                id,
                name
            }
        });
    }

    const deleteItem = () => {
        const deleteHandle = async () => {
            return getItemDelete(id);
        }
        deleteAlert({ title: "Are you sure?" }, deleteHandle)
            .then((res) => {
                if (res === null) {
                    return;
                }
                if (res.flag) {
                    enqueueSnackbar("Item delete success", {
                        variant: 'success',
                    });
                    history.push({
                        pathname: pages.ITEM_LIST,
                    });
                } else {
                    enqueueSnackbar("Item delete failed", {
                        variant: 'error',
                    });
                }
            });

    }

    if (Object.keys(itemData).length === 0) {
        return (<>
            <SuiBox
                component="li"
                display="flex"
                justifyContent="space-between"
                alignItems="flex-start"
                backgroundColor="white"
                borderRadius="lg"
                p={3}
                mb={1}
                mt={2}
            >
                <Grid container spacing={2}>
                    <Grid item xs={12}>
                        <Skeleton animation="wave" variant="text" width="10%" height={40} />
                    </Grid>
                    <Grid item xs={12}>
                        <Skeleton animation="wave" variant="text" width="30%" height={30} />
                    </Grid>
                    <Grid item xs={12}>
                        <Skeleton animation="wave" variant="text" width="20%" height={30} />
                    </Grid>
                    <Grid item xs={12}>
                        <Skeleton animation="wave" variant="text" width="40%" height={30} />
                    </Grid>
                    <Grid item xs={12}>
                        <Skeleton animation="wave" variant="text" width="20%" height={30} />
                    </Grid>
                </Grid>
            </SuiBox>
        </>)
    }
    return (
        <>
            <SuiBox
                component="li"
                display="flex"
                justifyContent="space-between"
                alignItems="flex-start"
                backgroundColor="white"
                borderRadius="lg"
                p={3}
                mb={1}
                mt={2}
            >
                <SuiBox width="100%" display="flex" flexDirection="column">
                    <SuiBox
                        display="flex"
                        justifyContent="space-between"
                        alignItems={{ xs: "flex-start", sm: "center" }}
                        flexDirection={{ xs: "column", sm: "row" }}
                        mb={2}
                    >
                        <SuiTypography fontWeight="bold" textColor="primary" textTransform="uppercase">
                            {itemData.name}
                        </SuiTypography>
                        <SuiBox display="flex" alignItems="center" mt={{ xs: 2, sm: 0 }} ml={{ xs: -1.5, sm: 0 }}>
                            <SuiBox mr={1}>
                                <SuiButton buttonColor="error" onClick={deleteItem}>
                                    <Icon className="material-icons-round">delete</Icon>&nbsp;delete
                                </SuiButton>
                            </SuiBox>
                            <SuiButton buttonColor="dark" onClick={updateItem}>
                                <Icon className="material-icons-round">edit</Icon>&nbsp;edit
                            </SuiButton>
                        </SuiBox>
                    </SuiBox>
                    <Grid container>
                        <Grid item xs={3}>
                            <SuiTypography fontWeight="regular">
                                Code
                            </SuiTypography>
                        </Grid>
                        <Grid item xs={9}>
                            <SuiTypography fontWeight="medium">
                                :&nbsp;{itemData.code}
                            </SuiTypography>
                        </Grid>
                        <Grid item xs={3}>
                            <SuiTypography fontWeight="regular">
                                Available
                            </SuiTypography>
                        </Grid>
                        <Grid item xs={9}>
                            <SuiTypography fontWeight="medium">
                                :&nbsp;{itemData.avi_qty}
                            </SuiTypography>
                        </Grid>
                        <Grid item xs={3}>
                            <SuiTypography fontWeight="regular">
                                Min Qty
                            </SuiTypography>
                        </Grid>
                        <Grid item xs={9}>
                            <SuiTypography fontWeight="medium">
                                :&nbsp;{itemData.min_qty}
                            </SuiTypography>
                        </Grid>
                        <Grid item xs={3}>
                            <SuiTypography fontWeight="regular">
                                Expire Time
                            </SuiTypography>
                        </Grid>
                        <Grid item xs={9}>
                            <SuiTypography fontWeight="medium">
                                :&nbsp;{itemData.expire_time}
                            </SuiTypography>
                        </Grid>
                        <Grid item xs={3}>
                            <SuiTypography fontWeight="regular">
                                Create At
                            </SuiTypography>
                        </Grid>
                        <Grid item xs={9}>
                            <SuiTypography fontWeight="medium">
                                :&nbsp;{itemData.create_at}
                            </SuiTypography>
                        </Grid>
                    </Grid>
                </SuiBox>
            </SuiBox>
        </>
    );
}

export default ItemView;
