
// Soft UI Dashboard React example components
import { useForm } from "react-hook-form";
import { useSnackbar } from 'notistack';
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";
import ItemForm from "./_form";
import { useHistory } from "react-router-dom";
import { pages, pagesTitle } from "links/pages";
// Custom styles for the Tables

// Service
import { addItem } from "../service"

const schema = yup.object({
    // image: yup.mixed().test("fileSize", "Max file size is 1MB", (value, ob1) => {
    //     console.log(value, ob1);
    //     return !value || value[0].size < 2000000;
    // }).test("type", "Only jpg,jpeg and png are supported", (value) => {
    //     return !value || (value[0].type === "image/jpeg" || value[0].type === "image/jpg" || value[0].type === "image/png");
    // }),
    name: yup.string().required("Name cannot be blank").max(100),
    code: yup.string().required("Code cannot be blank").max(100),
    min_qty: yup.number().positive().integer().required("Min Qty cannot be blank"),
    expire_time: yup.number().positive().integer("Life must be number"),
});

function ItemAdd() {
    const { handleSubmit, control, formState: { errors } } = useForm({
        resolver: yupResolver(schema),
    });
    const { enqueueSnackbar } = useSnackbar();
    const history = useHistory();
    async function onSubmitHandler(data) {
        await addItem(data).then(({ flag, data }) => {
            if (flag) {
                enqueueSnackbar("Item add success", {
                    variant: 'success',
                });
                history.push({
                    pathname: pages.ITEM_VIEW,
                    search: "?" + (data.name.replace(" ", "-").toLowerCase()),
                    state: {
                        id: data.id,
                        name: data.name
                    }
                });
            }

        })
    };
    return (
        <ItemForm
            onSubmitHandler={onSubmitHandler}
            handleSubmit={handleSubmit}
            control={control}
        />
    );
}

export default ItemAdd;
