/* eslint-disable react/prop-types */
// Soft UI Dashboard React components
import SuiBox from "components/SuiBox";
import SuiTypography from "components/SuiTypography";
import SuiAvatar from "components/SuiAvatar";
import SuiBadge from "components/SuiBadge";
// Images
import team2 from "assets/images/team-2.jpg";
import Skeleton from '@mui/material/Skeleton';

const stat = {
  "1": {
    "batch": "active",
    "color": "success"
  },
  "0": {
    "batch": "inactive",
    "color": "secondary"
  }
};
function Pimg({ image, name, code }) {
  return (
    <SuiBox display="flex" alignItems="center" px={1} py={0.5}>
      <SuiBox mr={2}>
        <SuiAvatar src={image} alt={name} size="sm" variant="rounded" />
      </SuiBox>
    </SuiBox>
  );
}

function Name({ name, code }) {
  return (
    <SuiBox display="flex" alignItems="center" >
      <SuiBox display="flex" flexDirection="column">
        <SuiTypography variant="button" fontWeight="medium">
          {name}
        </SuiTypography>
        <SuiTypography variant="caption" textColor="secondary">
          {code}
        </SuiTypography>
      </SuiBox>
    </SuiBox>
  );
}

function Qty({ available, min }) {
  return (
    <SuiBox display="flex" flexDirection="column">
      <SuiTypography variant="caption" fontWeight="medium" textColor="text">
        {available}
      </SuiTypography>
      <SuiTypography variant="caption" textColor="secondary">
        {min}
      </SuiTypography>
    </SuiBox>
  );
}

function Status({ status }) {
  return (
    <SuiBadge variant="gradient" badgeContent={stat[status].batch} color={stat[status].color} size="extra-small" />
  );
}
const modelList = (list, handleView) => {
  return list.map(({ id, name, code, status, min_qty, avi_qty, expire_time }) => {
    return {
      image: <Pimg image={team2} name={name} />,
      name: <Name code={code} name={name} />,
      min: min_qty,
      avi: avi_qty,
      life: expire_time,
      status: <Status status={status} />,
      action: (
        <a style={{ cursor: "pointer" }} onClick={() => { handleView(id, name) }}>
          <SuiTypography
            variant="caption"
            textColor="secondary"
            fontWeight="medium"
          >View</SuiTypography>
        </a>
      ),
    }
  });
}

const modelListEmpty = () => {
  return [
    {
      image: [
        { "colSpan": "7", style: { textAlign: "center" } },
        <SuiTypography
          component="span"
          textColor="secondary"
          fontWeight="sm"
          p={20}
        >No data found</SuiTypography>
      ],
    }
  ]
}

const modelListInit = () => {
  return [
    {
      image: <Skeleton animation="wave" style={{ margin: "5px 10px" }} variant="circular" width={40} height={40} />,
      name: [
        { "colSpan": "6" },
        <Skeleton animation="wave" variant="text" width="80%" height={30} />
      ],
    },
    {
      image: <Skeleton animation="wave" style={{ margin: "5px 10px" }} variant="circular" width={40} height={40} />,
      name: [
        { "colSpan": "6" },
        <Skeleton animation="wave" variant="text" width="70%" height={30} />
      ],
    },
    {
      image: <Skeleton animation="wave" style={{ margin: "5px 10px" }} variant="circular" width={40} height={40} />,
      name: [
        { "colSpan": "6" },
        <Skeleton animation="wave" variant="text" width="90%" height={30} />
      ],
    }
  ]
}

export default {
  columns: [
    { name: "image", align: "left" },
    { name: "name", align: "left" },
    { name: "avi", label: "Available", align: "left" },
    { name: "min", align: "left" },
    { name: "life", align: "left" },
    { name: "status", align: "center" },
    { name: "action", align: "center" },
  ],
  modelList,
  modelListInit,
  modelListEmpty
};
